.featured{
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: column;
    margin-top: 30px;
    margin-bottom: 30px;
    color: #fff;
}
.featured_heading{
    width: 100%;
    display: flex;
    align-items: flex-start;
    justify-content: flex-start;
    margin-bottom: 20px;
}
.featured_cards{
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-around;
}